<template>
  <div v-loading="loading">
    <el-button type="success" @click="$refs.editable.insert()"></el-button>
    <el-button type="success" @click="$refs.editable.insertAt(null, -1)"></el-button>
    <el-button type="danger" @click="$refs.editable.removeSelecteds()"></el-button>
    <el-button type="info" @click="$refs.editable.revert()"></el-button>
    <el-button type="info" @click="$refs.editable.clear()"></el-button>
    <el-button type="warning" @click="submitEvent">&</el-button>
    <el-button type="primary" @click="getInsertEvent"></el-button>
    <el-button type="primary" @click="getUpdateEvent"></el-button>
    <el-button type="primary" @click="getRemoveEvent"></el-button>
    <el-button type="primary" @click="getAllEvent"></el-button>
    <el-editable ref="editable" stripe border size="medium" height="600" style="width: 100%" :editRules="validRules">
      <el-editable-column type="index" width="55"></el-editable-column>
      <template v-for="(item, index) in columnConfigs">
        <el-editable-column v-if="item.show" :key="index" v-bind="item"></el-editable-column>
      </template>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope.row, scope.$index)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'
import columnsData from '@/common/json/editable/columns.json'

export default {
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (parseInt(value) < 18 || parseInt(value) > 28) {
            callback(new Error('18-28'))
          } else {
            callback()
          }
        }
      }, 50)
    }
    const checkRate = (rule, value, callback) => {
      if (parseInt(value || 0) < 2) {
        callback(new Error('2'))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      columnConfigs: [],
      sexList: [],
      regionList: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' }
        ],
        nickname: [
          { min: 5, max: 15, message: ' 5  15 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ],
        birthdate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        rate: [
          { validator: checkRate, trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let sexPromise = this.getSexJSON()
      let regionPromise = this.getRegionJSON()
      this.findList()
      this.getColumnConfigs().then(data => {
        this.columnConfigs = data.map(column => {
          column.checked = true
          column.show = true
          switch (column.prop) {
            case 'sex':
              column.editRender.options = []
              sexPromise.then(rest => {
                column.editRender.options = rest
              })
              break
            case 'region':
              column.editRender.attrs = {options: []}
              regionPromise.then(rest => {
                column.editRender.attrs.options = rest
              })
              break
            case 'birthdate':
              column.editRender.attrs = {
                type: 'date',
                format: 'yyyy-MM-dd'
              }
              break
            case 'rate':
              column.editRender.type = 'visible'
              break
          }
          return column
        })
      })
    },
    findList () {
      this.loading = true
      this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    removeEvent (row, index) {
      this.$refs.editable.removeRow(index)
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
        this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
          this.findList()
        })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 100)
      })
    },
    getColumnConfigs () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(columnsData, true)), 100)
      })
    },
    getDataJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(listData, true)), 350)
      })
    },
    getRegionJSON () {
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>



